<template>
  <div id="divadddata" class="w-100">
    <loader :is-visible="isLoading"></loader>
    <section id="divMainContent" class="main-content">
      <div id="PartialView_SiteMapLink" class="headingcont margintop10 marginleft_2" :data-title="title" style="display:none;">
      </div>
      <div class="user-guide-custom d-none" v-if="loggedUserInfo && loggedUserInfo.UserType === 'CA'">
        <div v-html="userGuideImport"></div>
      </div>
      <section class="main-content">
        <div class="container-fluid padding-t_10 px-0">
          <input id="currentController" name="currentController" type="hidden" value="BillingRule" />
          <input id="currentAction" name="currentAction" type="hidden" value="ImportFormatListing" />
          <input id="currentBreadcrumbAction" name="currentBreadcrumbAction" type="hidden" value="Import" />
          <div v-if="loggedUserInfo && loggedUserInfo.UserType === 'CA'">
            <!-- Assuming _SetupTabs is a component or partial, need to implement -->
            <!-- <setup-tabs></setup-tabs> -->
          </div>
          <div class="col-md-12 p-0">
            <div class="" id="myTabContent">
              <div class="tab-pane call-detail fade show active border-0 p-0" id="General" role="tabpanel" aria-labelledby="General-tab">
                <div v-if="loggedUserInfo && loggedUserInfo.UserType === 'CA'">
                  <section class="page-action">
                    <div class="container-fluid border-0 m-0 p-0">
                      <div class="w-100">
                        <div class="col-md-12 p-0">
                          <div class="theme-primary partition-full">
                            <span class="p-name text-white">{{ $t('Add') }} {{ $t('Setup') }}</span>
                          </div>
                        </div>
                      </div>
                    </div>
                  </section>
                </div>
                <div class="container-fluid border pt-2 pb-2">
                  <div class="billing-rule">
                    <dynamic-form ref="dynamicform" lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit" :mandatorytext="$t('MandatoryString')" >
                      <template v-slot:tgslot-SampleFile="{ data }">
                        <div class="col-lg-12 col-xl-12 p-0">
                          <div class="form-group">
                            <div class="input-group mb-3">
                              <div class="form-control">
                                <span class="text-truncate">Download Import Sample File</span>
                              </div>
                              <div class="input-group-append">
                                <a href="javascript:;" class="input-group-text" title="Download" checkprivilege="no">
                                  <i class="fa fa-download action-icon"></i>
                                </a>
                              </div>
                            </div>
                          </div>
                        </div>
                      </template>
                    </dynamic-form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </section>
  </div>
</template>

<script>
import { locateBillingService } from '../../services/locateBillingService'
import { useloggedUserInfo } from '../../stores/UserInfo'
import Loader from '../../components/Common/Loader.vue'

export default {
  name: 'Import',
  components: {
    Loader
  },
  data() {
    return {
      isLoading: false,
      title: '',
      userGuideImport: '',
      FormSchema: [{
        layoutType: "double",
        Data: [{
          astype: "TextField",
          label: this.$t('Setup') + ' ' + this.$t('Name'),
          name: "SetupName",
          value: "",
          placeholder: "",
          visibility: true,
          validationRules: {
            "required": true
          },
        },
        {
          astype: "NormalFileField",
          label: this.$t('UploadFile'),
          name: "FileSrc",
          placeholder: "",
          validationRules: {
            "required": true
          },
          FieldIndex: 0,
          config: {
            maxSize: 10,
            showSelectedFiles: [],
            accept: [
              { fileType: 'xlsx', iconClass: 'fa-file-excel-o' },
              { fileType: 'xls', iconClass: 'fa-file-excel-o' },
              { fileType: 'csv', iconClass: 'fa-file-text-o' }
            ],
            onDeleteSelectedFiles: this.onDeleteSelectedFiles,
            onChange: this.GetBase64String,
            showAddIcon: false,
            onAddButtonClick: '',
            multiple: false
          },
        }]
      }],
      buttons: [{
        type: "submit",
        class: "btn btn-info",
        text: "<i class='fa fa-download pr-2'></i>" + this.$t('Import'),
      },
      {
        type: "button",
        class: "btn btn-danger",
        text: "<i class='fa fa-close pr-2'></i>" + this.$t('Cancel'),
        onClick: this.cancel,
      }],
      file: null
    }
  },
  computed: {
    loggedUserInfo() {
      return useloggedUserInfo().GetUserInfo
    }
  },
  created() {
    this.title = this.$t('Import')
    this.userGuideImport = this.$t('User_Guide_Import_Billing_Setup') || ''
  },
  methods: {
     GetBase64String: function (evt) {
            try {
                var vm = this;
                vm.fileData = [];
                var files = evt.target.files; // FileList object
                vm.attachmentName = files[0].name;
                for (var i = 0; i < files.length; i++) {
                    var f = files[i];
                    var reader = new FileReader();
                    // Closure to capture the file information.
                    reader.onload = (function (theFile) {
                        return function (e) {
                            var binaryData = e.target.result;
                            //Converting Binary Data to base 64
                            var base64StringEncoded = window.btoa(binaryData);
                            vm.base64String = base64StringEncoded;
                            //showing file converted to base64

                            var fileName = f.name;
                            //vm.fileName = fileName;
                            var n = fileName.lastIndexOf(".");
                            var extensionName = fileName.substring(n + 1, fileName.length);

                            //vm.fileExt += "." + extensionName;
                            var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                            var fileSize = f.size;
                            //vm.fileSize = fileSize;
                            var fileObj = {
                                attachement_mime: attachemnetMime,
                                fileName: fileName,
                                userId: vm.GetUserInfo.ID,
                                companyId: vm.GetUserInfo.CompanyId,
                                id: 0,
                                moduleName: "initialsetup",
                                subModuleCode: "BulkUploaad",
                                containerName: vm.GetUserInfo.ContainerName,
                                base64String: base64StringEncoded,
                                attachement_path: ''
                            };

                            vm.fileData.push(fileObj);
                        };
                    })(f);
                    // Read in the image file as a data URL.
                    reader.readAsBinaryString(f);
                }
            }
            catch (ex) {
            }
        },
    onDeleteSelectedFiles() {
      this.file = null
    },
    async onSubmit(formfields) {
      this.isLoading = true
      var vm=this;
      debugger
      let obj = {
          file_Name: this.fileData[0].fileName,
          attachment: JSON.stringify(this.fileData)
       };
            let formData = new FormData();
            formData.append("postString", JSON.stringify(obj));
            const response = await locateBillingService.UploadFile(formData);
             if (response.data && response.data.length > 0) {
                    if (response.data[0].Name.length > 0) {
                        vm.filePath = response.data[0].Name;
                    }
                }
            var filePath = vm.filePath;
             var url = `templateName=${formfields.SetupName}` +
                `&excelfilepath=${encodeURIComponent(filePath)}`;
                debugger
            locateBillingService.ImportSetupDataFromExcel(url).then((response) => {
                vm.isLoading = false;
                if(response.data){
                  vm.SetupId=response.data.setupId;
                  vm.$router.push({ name: 'SetupTemplate', params: { setupId: vm.SetupId, isEditable : 0, Update: false} });
                }
            })
    },
    cancel() {
      this.$router.push({ name: 'ImportFormatListing' })
    }
  }
}
</script>

<style scoped>
/* Add any scoped styles if needed */
</style>